@import '../../../../assets/css/theme';

.token_view_icon {
  width: 28px;
  height: 28px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  color: #fff;
  border-radius: 50%;
  text-align: center;
  display: inline-block;
  line-height: 30px;
  cursor: pointer;
  &:hover {
    border: 1px solid $color-a-hover;
    color: $color-a-hover;
  }
}

.token_logo_icon {
  font-size: 14px;
  font-family: PingFangSC-Semibold, PingFang SC;
  font-weight: 600;
  color: $white;
  margin-right: 10px;
  img {
    width: 20px;
    height: 20px;
    margin-right: 6px;
    border-radius: 50%;
  }
}
