@import "../../../assets/css/mixins";

.pool_list {
  @include common-content-width;
  margin: 60px auto 0;
  &_logo, &_logo_right {
    width: 20px;
    height: 20px;
    border-radius: 50%;
  }
  &_logo_right {
    transform: translateX(-6px);
  }
  &_btns {
    &_add, &_remove {
      width: 120px;
      height: 30px;
      background: #8C51FF;
      border-radius: 4px;
      font-size: 14px;
      font-weight: 600;
      color: #FFFFFF;
      line-height: 30px;
    }
    &_remove {
      background-color: transparent;
      margin-left: 20px;
      color: #C1A0FF;
    }
  }
}