.liquidity_item {
  &_wrap {
    background: #362D4A;
    border-radius: 5px;
    margin-bottom: 10px;
  }
  &_header {
    padding: 10px;
    display: flex;
    align-items: center;
  }
  &_name {
    font-size: 14px;
    font-weight: 600;
    color: #C1A0FF;
    line-height: 20px;
    margin-left: 10px;
  }
  &_content {
    display: flex;
    padding: 0 10px 12px;
    justify-content: space-between;
  }
  &_info {
    display: flex;
    align-items: center;
    &:first-child {
      margin-bottom: 8px;
    }
    &_info_label {
      font-size: 12px;
      font-weight: 400;
      color: rgba(255,255,255, .5);
      line-height: 17px;
      margin-right: 4px;
    }
    &_info_value {
      font-size: 12px;
      font-weight: 600;
      color: #FFFFFF;
      line-height: 17px;
    }
  }
  &_footer {
    display: flex;
    align-items: stretch;
    justify-content: center;
    height: 35px;
    border-top: 1px solid rgba(255, 255, 255, .1);
    &_btn {
      flex: 1;
      font-size: 14px;
      font-weight: 600;
      color: #C1A0FF;
      border-right: 1px solid rgba(255, 255, 255, .1);
      text-align: center;
      line-height: 35px;
      &:last-child {
        border: none;
      }
      &_disable {
        opacity: 0.5;
        cursor: no-drop;
      }
    }
  }
  &_migrate_btn {
    margin-left: auto;
    width: 110px;
    height: 25px;
    background: #DECDFF;
    border-radius: 13px;
    font-size: 12px;
    font-weight: 600;
    color: #5A14E1;
    line-height: 25px;
    cursor: pointer;
    text-align: center;
  }
  &_token_icon {
    width: 20px;
    height: 20px;
    background: #1BA27A;
    border-radius: 50%;
  }
}