@import '../../../../assets/css/theme';

.swap-block {
  width: 100%;
  border-radius: 10px;
  position: relative;
  z-index: 2;

  .sunswap-chose-token {
    background-color: $blue;
    border-radius: 5px;
    color: $white;
    padding: 4px 10px 4px 10px;
    font-size: 14px;
  }
  .select-token-top {
    height: 28px;
  }

  .dragDown {
    margin: 0 10px 0 0;
    img {
      width: 20px;
      height: 20px;
      border-radius: 50%;
    }
    span {
      font-size: 16px;
      font-weight: 600;
      color: $white;
    }
  }

  .token-detail {
    width: 28px;
    height: 28px;
    border-radius: 50%;
  }

  .input-block {
    width: 100%;
    background: $color-input-bg;
    border-radius: 10px;
    padding: 14px 10px 14px 16px;

    .input-number {
      width: calc(100% - 86px);
      margin-right: 10px;
      flex: 1;
      input {
        width: 100%;
      }
    }

    input {
      font-size: 30px;
      font-family: Helvetica-Bold, Helvetica;
      font-weight: bold;
      background: $color-input-bg;
      color: $white;
      border: none;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      &:focus {
        outline: none;
      }
      &:placeholder {
        font-size: 30px;
      }
    }
    input[placeholder] {
      font-size: 30px;
    }
  }

  .swap-balance {
    font-size: 14px;
    > span:nth-child(1) {
      color: $white;
      opacity: 0.5;
    }
    > span:nth-child(2) {
      color: $white;
    }
  }

  .max-btn {
    background: $max-btn-bg;
    border-radius: 15px;
    border: 1px solid $max-btn-border;
    padding: 4px 14px;
    color: $white;
    font-size: 12px;
    font-weight: 600;
    cursor: pointer;
    white-space: nowrap;
  }
}
.media-trade-router-pc {
  display: block;
}
.media-trade-router-mobile {
  display: none;
}
@media screen and (max-width: 768px) {
  .swap-block .input-block input[placeholder] {
    font-size: 20px;
  }
  #root .media-trade-router-pc {
    display: none;
    &.flex {
      display: none;
    }
  }
  .media-trade-router-mobile {
    display: block;
  }
}
