.warning_tip {
  background: rgba(255, 172, 0, .1);
  border-radius: 10px;
  padding: 10px 20px;
  &_title {
    font-size: 16px;
    font-weight: 600;
    color: #FFAC00;
    line-height: 22px;
    margin-bottom: 12px;
  }
  &_content {
    font-size: 14px;
    font-weight: 400;
    color: rgba(255, 172, 0, .5);
    line-height: 20px;
  }
}