@import '../../../../assets/css/theme';
@import './inputBlock.scss';

.swap-content {
  background: transparent;
  &.ant-layout {
    background: transparent;
  }

  .flex-swap-block {
    width: 100%;
  }

  .swap-arrow {
    width: 46px;
    height: 46px;
    cursor: pointer;
    margin-bottom: 8px;
    background: url('../../../../assets/images/swap-arrow-right.png') 100%/100% no-repeat;
    &:hover {
      background: url('../../../../assets/images/swap-double-arrow.png') 100%/100% no-repeat;
      transform: rotate(90deg);
    }
  }

  .trade-router-loading {
    color: $white;
    font-size: 14px;
  }

  .price-loading-img {
    position: relative;
    &:after {
      animation: loadingPoint 2000ms infinite;
      position: absolute;
      content: '';
      left: 10px;
      top: 50%;
      transform: translateY(-50%);
      letter-spacing: 5px;
      font-size: 20px;
    }
  }

  @keyframes loadingPoint {
    25% {
      content: '·';
    }
    50% {
      content: '··';
    }
    75% {
      content: '···';
    }
    100% {
      content: '';
    }
  }

  .rates {
    font-size: 14px;
    font-family: PingFangSC-Regular, PingFang SC;
    font-weight: 400;
    color: rgba(255, 255, 255, 0.5);
    .token-rates {
      font-size: 14px;
      font-family: PingFangSC-Semibold, PingFang SC;
      font-weight: 600;
      color: $white;
      margin-left: 4px;
    }
    img {
      width: 20px;
      height: 20px;
    }
  }

  .add-recipient {
    font-size: 16px;
    font-weight: 600;
    color: $white;
    height: 28px;
    line-height: 28px;
    cursor: pointer;
    display: inline-block;
    &-remove {
      color: #ffac00;
    }
    .recipient-icon {
      width: 28px;
      height: 28px;
    }
  }

  .recipient-list {
    input {
      width: 100%;
      background: $color-input-bg;
      border-radius: 10px;
      border: none;
      padding: 10px 16px;
      outline: none;
      font-size: 14px;
      font-family: PingFangSC-Regular, PingFang SC;
      font-weight: 400;
      color: $white;
      opacity: 0.5;
      box-sizing: border-box;
    }
    .link {
      color: $color-a;
      font-size: 12px;
      margin-top: 10px;
    }
  }

  .sunswap-trade-router {
    font-size: 14px;
    font-weight: 600;
    color: $white;
    img {
      width: 18px;
      height: 18px;
    }
    .trade-route {
      font-size: 14px;
      font-family: PingFangSC-Regular, PingFang SC;
      font-weight: 400;
      color: #ffffff;
    }
    .tr-arrow {
      width: 8px;
      height: 8px;
      background: url(../../../../assets/images/homeTokens/arrow_default.png) 0 0 no-repeat;
      background-size: 100% 100%;
      margin: 0 8px;
    }
    li {
      &:last-child {
        .tr-arrow {
          display: none;
        }
      }
    }
  }

  .detail-item {
    color: rgba($white, 0.5);
    font-weight: 300;
    font-family: PingFangSC-Light, PingFang SC;
    font-size: 14px;
    > span {
      font-size: 14px;
      font-family: PingFangSC-Semibold, PingFang SC;
      font-weight: 600;
      color: $white;
      width: min-content;
      flex: 1;
    }
    .red {
      color: $red;
    }
    .orange {
      color: #ffac00;
    }
  }
  .action-btns {
    margin-top: 40px;
  }
}
.swap-content,
.swap-and-pool {
  .safe-amount-tip {
    font-size: 12px;
    font-family: PingFangSC-Regular, PingFang SC;
    font-weight: 400;
    color: $white;
    line-height: 20px;
    margin-top: 20px;
    .safe-amount-set-btn {
      color: $color-a;
      cursor: pointer;
    }
  }
}
.swap-and-pool .safe-amount-tip {
  margin-top: 0;
}

@media screen and (max-width: 576px) {
  .swap-content .swap-arrow {
    width: 30px;
    height: 30px;
    background: url('../../../../assets/images/swap-arrow-down.png') 100%/100% no-repeat;
    flex-shrink: 0;
    &:hover {
      background: url('../../../../assets/images/swap-arrow-down.png') 100%/100% no-repeat;
      transform: rotate(0deg);
    }
  }
}
