@import '../../../../assets/css/theme';
@import "../../../../assets/css/var";
.sun_swap_content {
  background: $color-table-bg;
  border-radius: 0 0 10px 10px;
  padding: 0 40px 30px;
  &_detail {
    padding-top: 30px;
    border-radius: 10px;
  }
}

.page_tab_wrap {
  background: #362d4a;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 40px 30px;
}

.page_tab_nav_wrap {
  position: relative;
}
.v3_new_icon {
  position: absolute;
  left: -23px;
  top: -16px;
  z-index: 1;
  width: 138px;
  height: 77px;
  @include mobile-only {
    width: 64px;
    height: 35px;
    left: -10px;
    top: -12px;
  }
}

@media screen and (max-width: 750px) {
  .page_tab_wrap {
    padding: 12px 10px;
  }
  .sun_swap_content {
    padding: 10px;
  }
}
