.v3-sub-nav {
  color: #fff;
  display: flex;
  align-items: center;
  margin-bottom: 20px;
  &-title {
    margin-left: 10px;
    font-size: 20px;
    font-weight: 600;
    color: #FFFFFF;
    line-height: 28px;
  }
  &-right {
    margin-left: auto;
  }
}
.v3-ques-icon {
  color: #999;
  display: inline-block;
  background: #fff;
  min-width: 18px;
  width: 18px;
  height: 18px;
  line-height: 18px;
  text-align: center;
  border-radius: 100%;
  border: 1px solid #999;
  font-size: 16px;
  vertical-align: middle;
}