@import '../../../../assets/css/theme';

.remove_liq_title {
  display: flex;
  align-items: center;
  font-size: 16px;
  font-family: PingFangSC-Semibold, PingFang SC;
  font-weight: 600;
  color: $white;
  img {
    width: 24px;
    height: 24px;
  }
}

.remove_liq_fee {
  font-family: PingFangSC-Semibold, PingFang SC;
  font-weight: 600;
  color: $white;
  > span:nth-child(1) {
    font-size: 10px;
    opacity: 0.5;
  }
  > span:nth-child(2) {
    font-size: 14px;
  }
}

.remove_liq_num {
  font-size: 50px;
  font-family: Helvetica-Bold, Helvetica;
  font-weight: bold;
  color: $white;
  margin-top: 10px;
}

.remove_liq_btn {
  background: rgba(193, 160, 255, 0.1);
  border-radius: 5px;
  border: 1px solid rgba(255, 255, 255, 0.15);
  font-size: 14px;
  font-family: PingFangSC-Semibold, PingFang SC;
  font-weight: 600;
  color: $color-a;
  padding: 4px 16px;
}

.remove_liq_active_btn {
  background: $blue;
  border-radius: 5px;
  font-size: 14px;
  font-family: PingFangSC-Semibold, PingFang SC;
  font-weight: 600;
  color: $white;
  padding: 4px 16px;
}

.remove_liq_token {
  font-size: 14px;
  font-family: PingFangSC-Semibold, PingFang SC;
  font-weight: 600;
  color: $white;
  > img {
    width: 20px;
    height: 20px;
    margin-right: 6px;
    border-radius: 50%;
  }
}

.remove_liq_value {
  font-size: 16px;
  font-family: PingFangSC-Semibold, PingFang SC;
  font-weight: 600;
  color: $white;
  text-align: center;
}

.remove_liq_price {
  width: fill-available;
  width: -webkit-fill-available;
  margin: 0 25px;
  text-align: center;
  > p {
    position: relative;
    font-size: 12px;
    font-family: PingFangSC-Regular, PingFang SC;
    font-weight: 400;
    color: $white;
    opacity: 0.3;
    opacity: 0.3;
    &:nth-child(1) {
      border-bottom: 1px solid $white;

      > span {
        position: absolute;
        right: -6px;
        bottom: 7px;
        width: 10px;
        height: 10px;
        border-left: 1px solid $white;
        transform: rotate(-45deg);
        transform-origin: right bottom;
      }
    }
    &:nth-child(2) {
      border-top: 1px solid $white;
      margin-top: 7px;
      > span {
        position: absolute;
        left: 0px;
        top: 0px;
        left: 0;
        top: 0;
        width: 10px;
        height: 10px;
        border-left: 1px solid $white;
        transform: rotate(-45deg);
        transform-origin: left top;
      }
    }
  }
}
